body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* General Styles */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1.6;
  background-color: #f9f9f9;
}

h2, h3, h4 {
  color: #333;
}

p {
  color: #555;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: white;
  padding: 10px 20px;
}

.nav {
  display: flex;
  gap: 15px;
}

.nav-link {
  color: white;
  text-decoration: none;
  position: relative;
}

.nav-link:hover {
  text-decoration: underline;
}

.dropdown {
  position: relative;
}

.dropdown-toggle {
  background: none;
  border: none;
  color: white;
  font-size: inherit;
  cursor: pointer;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  overflow: hidden;
  z-index: 1000;
}

.dropdown-menu button {
  display: block;
  width: 100%;
  padding: 10px;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  font-size: 1rem;
}

.dropdown-menu button:hover {
  background-color: #f0f0f0;
}

/* General team container styles */
.team-container {
  text-align: center;
  padding: 20px;
}

/* Heading style */
.team-heading {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
}

/* Grid for team cards */
.team-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

/* Team card styling */
.team-card {
  width: 200px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  text-align: center;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.team-card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

/* Team member image styling */
.team-image {
  width: 100%;
  border-radius: 50%;
  margin-bottom: 10px;
}

/* Team member name styling */
.team-name {
  font-size: 1.2rem;
  margin: 10px 0;
  color: #222;
}

/* Team member role styling */
.team-role {
  font-size: 1rem;
  color: #555;
}
  
body {
  margin-top: 80px; /* Adjust based on the header's height to avoid content overlap */
}

.nav-links a {
  margin: 0 15px;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #ffcc00;
}

.search-bar input {
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  margin-right: 5px;
}

.search-bar button {
  background-color: #ffcc00;
  border: none;
  color: #003366;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;
}

.search-bar button:hover {
  background-color: #ffa500;
}

/* About Section */
.about-us {
  padding: 100px 20px;
  background-color: #fff;
}

.about-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
}

.about-item {
  flex: 1;
  min-width: 300px;
}

.about-text h2 {
  color: #003366;
}

.about-image {
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center;
  border-radius: 8px;
}

/* Services Section */
.services-container {
  padding: 60px 20px;
  text-align: center;
  background-color: #e6f2ff;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.service-card {
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  padding: 20px;
  text-align: center;
}

.service-description {
  font-size: 0.9rem;
  color: #555;
  line-height: 1.5;
}

/* Blog Section */
.blog-container {
  padding: 60px 20px;
  background-color: #fff;
}

.blog-post {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}

.blog-post-image img {
  width: 100%;
  border-radius: 8px;
}

.blog-post-content {
  flex: 1;
}

.blog-post-title {
  color: #003366;
}

.blog-read-more {
  color: #ff6600;
  text-decoration: none;
  font-weight: bold;
  transition: color 0.3s ease;
}

.blog-read-more:hover {
  color: #cc5200;
}

/* Contact Section */
.contact-us-container {
  padding: 60px 20px;
  background-color: #e6f2ff;
  text-align: center;
}

.contact-options {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.contact-option {
  flex: 1;
  max-width: 300px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.contact-form input, .contact-form textarea {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.contact-form button {
  padding: 10px 15px;
  background-color: #003366;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.contact-form button:hover {
  background-color: #002244;
}

/* Footer */
.footer-container {
  background-color: #003366;
  color: #fff;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
}

.footer-section {
  flex: 1;
  min-width: 200px;
}

.footer-list li {
  list-style: none;
  margin: 5px 0;
  color: #fff;
}

.footer-list li.Nav {
  font-weight: bold;
  margin-top: 10px;
}
